import React, { Component } from 'react';

import img1 from '../Assets/slider/img1.jpeg';
import img2 from '../Assets/slider/img2.jpeg';
import img3 from '../Assets/slider/img3.jpeg';
import logo from '../Assets/logo.png';
const images = [
  {
    'imgSrc':img1,
    'captionText':'Bogat Ukus'
  },
  {
    'imgSrc':img2,
    'captionText':'Najbolja Pizza U Gradu'
  },
  {
    'imgSrc':img3,
    'captionText':'Za Svakoga Po Nesto'
  }
];
let slideIndex = 1;

var timer = 5000;
class Slider extends Component{
  constructor(props){
    super(props)
    this.state = {
          opacity: 0,
          position:0,
          titleOpacity: 1
    }
    this.currentSlide = this.currentSlide.bind(this);
    this.plusSlides = this.plusSlides.bind(this);
    this.showSlides = this.showSlides.bind(this);
  }
  componentDidMount(){
      window.addEventListener('scroll',() => {
        let move = window.pageYOffset  / 1.2;
        let setOpacity = (window.pageYOffset + 1) - (window.pageYOffset * 1.002);

        if (window.innerWidth < 768) {
          setOpacity = (window.pageYOffset + 1) - (window.pageYOffset * 1.008);
        }
        if(setOpacity > 1) {
           this.setState({titleOpacity: 1})
         } else if (setOpacity < 0) {
            this.setState({titleOpacity: 0})
         } else{
           this.setState({titleOpacity: setOpacity})
         }
        this.setState({position:move})
      });

    const opacity = setInterval(()=>{
      this.setState({opacity:1})
      clearInterval(opacity);
    },100);

    setInterval(()=>{
      this.showSlides(slideIndex += 1);
    },timer)
  }

  showSlides(n) {
    let i;
    let slides = document.getElementsByClassName("mySlides");
    let dots = document.getElementsByClassName("dot");
    if(n > slides.length){slideIndex = 1};
    if(n < 1){slideIndex = slides.length};
    for (i = 0; i < slides.length; i++) {
        slides[i].style.display = "none";
    }
    for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active-slide", "");
    }
    slides[slideIndex-1].style.display = "block";
    dots[slideIndex-1].className += " active-slide";

  }

  currentSlide(props){
    this.showSlides(slideIndex = props);
  }

  plusSlides(props){
    this.showSlides(slideIndex += props);
  }


  render(){
    return(
    <div className='slider-wrapper' style={{opacity:this.state.opacity}}>
    <img src={`${logo}`} alt='logo' className='logo' />
    <div className='header-wrapper'>
    <div style={{position:'relative', top:`${this.state.position}px`}}>
    <h1 className='title' style={{opacity:this.state.titleOpacity}}>naziv firme</h1>
    <h2 className='subtitle' style={{opacity:this.state.titleOpacity}}>Vasa omiljena brza hrana</h2>
    </div>
    </div>
    <div className="slideshow-container" id='slider'>
    {
      images.map((src, index)=>{
        return(
          <div key={index} className="mySlides fade" style={{display:index === 0 ? 'block':'none'}}>

            <img src={src.imgSrc} alt='food slide' className='slider-image'  />
            <div className="text">{src.captionText}</div>
          </div>
        )
      })
    }


      <a className="prev" onClick={()=>this.plusSlides(-1)} >&#10094;</a>
      <a className="next" onClick={()=>this.plusSlides(1)} >&#10095;</a>


    </div>
    <br />
    <div style={{textAlign:'center'}}>
      <span className="dot active-slide" onClick={()=>this.currentSlide(1)} ></span>
      <span className="dot" onClick={()=>this.currentSlide(2)}></span>
      <span className="dot" onClick={()=>this.currentSlide(3)}></span>
      </div>

    </div>
    )
  }
}
export default Slider;



