import React, { Component } from 'react';
import ReactLoader from 'react-loading';

class Special extends Component{
  constructor(props){
    super(props)
    this.state = {
      data: [],
      loading:true,
      opacity: 0,
      value:'',
      imageStatus: false,
      imageOpacity: 0
    }
    this.handleImageLoaded = this.handleImageLoaded.bind(this);
    this.handleImageErrored = this.handleImageErrored.bind(this);

    fetch(`${process.env.PUBLIC_URL}/specijaliteti.json`)
    .then(response => response.json())
    .then(result => this.showResult(result))
    .catch(error => this.handleError(error))

  }
  componentDidMount(){

    const opacity = setInterval(()=>{
      this.setState({opacity:1})
      clearInterval(opacity);
    },100);
    const test = setInterval(()=>{
    this.setState({ imageStatus: true });

    const imageOpacity = setInterval(()=>{
    this.setState({ imageOpacity: 1 });
      clearInterval(imageOpacity);
    },300);

    clearInterval(test);
    },1500);


  }

  showResult(data){
    this.setState({data:data});
    this.setState({loading:false})
  }


  handleError(error){
    this.setState({value:'Trenutno nema podataka. Pokusajte kasnije'});
    this.setState({loading:false});
  }
  handleImageLoaded() {

  }

  handleImageErrored() {
    this.setState({ imageStatus: "Nema slike" });
  }
  render(){
    const {data,value, loading,imageStatus,imageOpacity} = this.state;
    return(
    <div className='wrapper'>
      {
        loading !== true ?

          <div className='special-wrapper' style={{opacity:this.state.opacity}}  >
          {
              data.response.map(item => {
                  if(item.size === null){item.size = ''}
                  let mark = "";
              return(
                <div key={item.id} className='special-box special' >

                    <h3>{item.name}</h3>
                    {
                      imageStatus === true ?
                      <img
                        alt={value}
                        className='special-img'
                        onClick={this.handleImgClick}
                        src={require(`../Assets/${item.img}`)}
                        onLoad={this.handleImageLoaded}
                        onError={this.handleImageErrored}
                        style={{opacity:imageOpacity}}
                      />
                    : <ReactLoader type='bubbles' color='#edbb4a' className='image-loader' />
                    }
                      <p className='special-description'>{item.description}</p>
                      <div className='special-description'>
                        <p>Sastav:</p>
                        <ol>
                          {
                            item.composition.map((i, index) => {
                            item.composition.length -1 > index ? mark = ", " : mark = ""
                            return (
                              <li key={index}>
                                 {i}{mark}
                               </li>
                            )
                            })
                          }
                        </ol>
                      </div>

                  <div className='special-price'>Cena:Cena:{`${item.price}din`}</div>
                </div>
              )
            })
          }
          </div>

          : <ReactLoader type='bubbles' color='#edbb4a' className='loader' />


      }
    </div>
    );


}
}
export default Special;



