import React, { Component } from 'react';
import {
  NavLink
 } from 'react-router-dom'

import fb from '../Assets/social/facebook.png';
import tw from '../Assets/social/twitter.png';
import ig from '../Assets/social/instagram.png';
import gp from '../Assets/social/google-plus.png';


class Footer extends Component{
  constructor(props){
    super(props);
    this.handleClickLink = this.handleClickLink.bind(this);
  }
  handleClickLink(){
    let headerHeight = document.getElementById('slider');
    const top = Math.floor(headerHeight.getBoundingClientRect().height);
    window.scroll({
      top: top,
      behavior: 'smooth'
    });
  }

  render(){
    return(
    <footer>
      <div className='footer-wrapper'>
        <div className='footer-content_1'>
          <ul className='footer-section'>
            <a href='http://www.google.com' target='_blank' rel="noopener noreferrer" className='social-link'>
              <img src={fb} alt='facebook logo' className='social-networks'/>
            </a>
            <a href='http://www.google.com' target='_blank' rel="noopener noreferrer" className='social-link'>
              <img src={ig} alt='instagram logo' className='social-networks'/>
            </a>
            <a href='http://www.google.com' target='_blank' rel="noopener noreferrer" className='social-link'>
              <img src={tw} alt='twitter logo' className='social-networks'/>
            </a>
            <a href='http://www.google.com' target='_blank' rel="noopener noreferrer" className='social-link'>
              <img src={gp} alt='google plus logo' className='social-networks'/>
            </a>
          </ul>
        </div>

        <div className='footer-content_2'>
              <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/pocetna' className='footer-nav-link'>Pocetna</NavLink> /
              <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/meni' className='footer-nav-link'>Meni</NavLink> /
              <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/specijaliteti' className='footer-nav-link'>Specijaliteti</NavLink> /
              <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/kontakt' className='footer-nav-link'>Kontakt</NavLink> /
              <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/onama' className='footer-nav-link'>O nama</NavLink>
        </div>
      </div>
    </footer>
    )
  }
}
export default Footer;



