import React, { Component } from 'react';
import {
  NavLink
} from 'react-router-dom';


class Nav extends Component {
  constructor(props){
    super(props);
    this.state = {
      width:'2',
      height:'0',
      rot:'0',
      headOpacity:'1',
      margin: 0
    }
    this.handleMenuOpen = this.handleMenuOpen.bind(this);
    this.handleMenuClose = this.handleMenuClose.bind(this);
    this.handleClickLink = this.handleClickLink.bind(this);
  }


  handleMenuOpen(){
    const {width} = this.state;
    this.setState({rot:'360'})
      const hei = setInterval(()=>{
        this.setState({height:'210',margin:20})
        if (window.innerWidth <= 768) {
          this.setState({headOpacity:"0.3"})
        }
        clearInterval(hei);
      },200)
    const wid = setInterval(()=>{
      width === '2'
      ? this.setState({width:'200'})
      : this.setState({width:'2'})
      clearInterval(wid);
    },400);
  }

  handleMenuClose(){
    const {height} = this.state;
    this.setState({width:'2'})
    const hei = setInterval(()=>{
      height === '0'
        ? this.setState({height:'210',margin:20})
        : this.setState({height:'0', margin:0})
        if (window.innerWidth <= 768) {
          this.setState({headOpacity:"1"})
        }

        clearInterval(hei);
      },100);
    const wid = setInterval(() =>{
        this.setState({rot:'0'})
        clearInterval(wid);
      },200);
  }

  handleClickLink(){
    let headerHeight = document.getElementById('slider');
    const top = Math.floor(headerHeight.getBoundingClientRect().height);
    window.scroll({
      top: top,
      left: 0,
      behavior: 'smooth'
    });

}

  render() {
    const {height, width, margin, rot} = this.state;
    return (

      <nav style={{transform:`rotateZ(${rot}deg)`}}>
      <div className='top-menu' onClick={height ==='0' ? this.handleMenuOpen: this.handleMenuClose}></div>

      <div className='menu'  style={{height:`${height}px`,width:`${width}px` }}>
      <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/pocetna' className='nav-link'>Pocetna</NavLink>
      <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/meni' className='nav-link'>Meni</NavLink>
      <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/specijaliteti' className='nav-link'>Specijaliteti</NavLink>
      <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/kontakt' className='nav-link'>Kontakt</NavLink>
      <NavLink onClick={()=>this.handleClickLink()} activeClassName="active" to='/onama' className='nav-link'>O nama</NavLink>
      </div>

      <div className='bottom-menu'
        onClick={
          height ==='0' ? this.handleMenuOpen: this.handleMenuClose
        }
        style={{marginTop:margin}}>
        </div>
      </nav>

    );
  }

}

export default Nav;



