import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  NavLink
} from 'react-router-dom';

//Components
import List from './List';


class Products extends Component {

  constructor(props){
    super(props);
    this.state = {
      opacity:0,
      url:this.props.match.url
    }
  }

  componentDidMount(){
    const opacity = setInterval(()=>{
      this.setState({opacity:1})
      clearInterval(opacity);
    },100);
  }

  render() {

    return (
      <Router basename={process.env.PUBLIC_URL}>
        <div className='wrapper' style={{opacity:this.state.opacity}}>
          <div className='subMenu'>
            <NavLink to={{ pathname: `${this.state.url}/rostilj` , state:{ target:'rostilj'}}}  activeClassName="active-subMenu" className='subMenu-link'>Rostilj</NavLink>
            <NavLink to={{ pathname: `${this.state.url}/pice`, state:{ target:'pice'}}} activeClassName="active-subMenu" className='subMenu-link'>Pizze</NavLink>
            <NavLink to={{ pathname: `${this.state.url}/palacinke`, state:{ target:'palacinke'}}} activeClassName="active-subMenu" className='subMenu-link'>Palacinke</NavLink>
          </div>
          <Route
          exact path={this.props.match.url}
          render={() =>
            <p className='menu-description'>
              Lorem ipsum dolor sit amet, omittam elaboraret pro no,
              has cu ubique latine, pri ne autem sadipscing appellantur.
              Tractatos cotidieque an duo. Usu ad odio eripuit consulatu.
              Putant consequat mea ut, vis quis constituam cu, ad error doctus expetendis quo.
              Per graeci appellantur voluptatibus an,
              mazim comprehensam in sea, ex aeterno debitis singulis nam.
            </p>
            }
          />
          <Route path={`${this.state.url}/rostilj`} component={List} />
          <Route path={`${this.state.url}/pice`} component={List} />
          <Route path={`${this.state.url}/palacinke`} component={List} />

        </div>
</Router>
    );
  }

}

export default Products;



