import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom';

import './App.css';
//component
import Nav from './Components/Nav';
import Home from './Components/Home';
import Products from './Components/Products';
import About from './Components/About';
import Contact from './Components/Contact'
import Special from './Components/Special';
import Footer from './Components/Footer';
import Slider from './Components/Slider';

import Arrow from './Assets/arrow-up.png'

//scroll
//ReallySmoothScroll.shim();


class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      viewArrow:false,
    }
    this.handleClick = this.handleClick.bind(this);

  }

componentDidMount(){
  let viewArrow = '';
  window.innerWidth > 768 ? viewArrow = false : viewArrow = true;
  this.setState({viewArrow:viewArrow});
}

handleClick(){
  window.scrollTo({
      top: 0,
      behavior: "smooth"
  });
}

  render() {

    return (
      <Router basename={process.env.PUBLIC_URL}>
      <div>
        <Nav />
        <Slider />

          {["/pocetna", "/"].map((path, index) =>
         <Route exact path={path} component={Home} key={index} />
          )}

        <Route path='/meni' component={Products} />
        <Route path='/kontakt' component={Contact} />
        <Route path='/onama' component={About} />
        <Route path='/specijaliteti' component={Special} />

        <img src={Arrow} alt='arrow up' onClick={this.handleClick} className='scroll-up' style={{display:this.state.viewArrow === true ? 'block' : 'none' }} />

        <Footer />
      </div>
      </Router>
    );
  }

}

export default App;



