import React, { Component } from 'react';
import ReactLoader from 'react-loading';

//component
class List extends Component {
  constructor(props){
    super(props);
    this.state = {
      data: [],
      loading:true,
      hasData:false,
      value:'',
      opacity: 0
    }
    this.handleImgClick = this.handleImgClick.bind(this);
  }

  componentDidMount(){
    const  target  = this.props.location.state.target;
    fetch(`${process.env.PUBLIC_URL}/${target}.json`)
    .then(response => response.json())
    .then(result => this.setResult(result))
    .catch(error => this.handleError(error))
  }

  setResult(data){

    this.setState({data:data});
    this.setState({value:this.props.location.state.target});

    this.setState({loading:false})
    this.setState({hasData:true});

    const opacity = setInterval(()=>{
      this.setState({opacity:1})
      clearInterval(opacity);
    },300);
  }

  handleError(error){
    this.setState({value:'Trenutno nema podataka. Pokusajte kasnije'});
    this.setState({loading:false});
  }

handleImgClick(){
  console.log('img click');
}


  render() {
    const {data,value, loading, hasData} = this.state;

    return (
      <div style={{margin:"15px 0"}} >
      {
        loading !== true ?
        hasData ?
          <div className='details-list' style={{opacity:this.state.opacity}}>
          {
              data.response.map(item => {
                if(item.size === null){item.size = ''}
                let mark = "";
            return(
              <div key={item.id} style={{margin:"45px 0"}}>
                <div className='product'>
                  <div className='product-title product-block'>{item.name}</div>
                    <img alt={value} className='product-img' onClick={this.handleImgClick} src={require(`../Assets/${value}/${item.img}`)}  />
                    <div className='product-description product-block'>{item.size}</div>
                      <div>(
                        {item.composition.map((i, index) => {
                          item.composition.length -1 > index ? mark = ", " : mark = ""
                          return (
                            <span key={index} className='product-composition'>
                               {i}{mark}
                             </span>
                          )
                          })
                        }
                      )</div>
                  </div>
                <div className='price product-block'>Cena:{`${item.price}din`}</div>
              </div>
            )
          })
        }
          </div>

        : <p style={{fontSize:'32px', color:'red'}}>{this.state.value}</p>
        : <ReactLoader type='bubbles' color='#edbb4a' className='loader' />

      }
      </div>
    );
  }
}





export default List;



