import React, { Component } from 'react'
import GoogleMapReact from 'google-map-react'

import marker from '../Assets/map/marker.png';

import '../Assets/css/map.css';

const AnyReactComponent = ({ text, addres }) => (
  <div className='map-marker'>
  <div className='loc-info'>
    <span>{text}</span>
    <span>{addres}</span>
  </div>
  <img src={marker} alt='map marker' />
  </div>
);


export default class Maps extends Component {

constructor(props){
  super(props)
  this.click = this.click.bind(this);
}
  click(){

  }



render() {
  console.log(this.props.addres);
    return (
      <div className='map'>
        <GoogleMapReact onClick={this.click}
        bootstrapURLKeys={{ key:'AIzaSyCI-ula-zHptrQUnG26vGEqcrGuuXv3hFQ' }}
          center={ this.props.center }
          zoom={10}>

          <AnyReactComponent
            lat={this.props.center.lat }
            lng={ this.props.center.lng}
            text={this.props.text }
            addres={this.props.addres}
          />

        </GoogleMapReact>
      </div>
    )
  }
}



