import React, { Component } from 'react';
import '../Assets/css/slider.css';
import food from '../Assets/bc/food.png';
import food2 from '../Assets/bc/food2.png';


const textEffect = [
  {
    "id":1,
    "text":"ivot bez ijedne gozbe dug je put na kome nema krme",
    "efc":"effect1"
  },
  {
    "id":2,
    "text":"Zdravlje na usta ulazi.",
    "efc":"effect2"
  },
  {
    "id":3,
    "text":"Dorukuj kao kralj, ruaj kao princ a veeraj kao prosjak.",
    "efc":"effect3"
  }
]

let id = 1;

class Home extends Component {
  constructor(props){
    super(props)
    this.state = {
      opacity: 0,
      text:null,
      effect:null
    }
  }
  componentDidMount(){
    const opacity = setInterval(()=>{
      this.setState({opacity:1})
      clearInterval(opacity);
    },100);

this.changeText(id += 1);
     setInterval(()=>{
       this.changeText(id += 1);
    },5000);
  }

changeText(n){
  for (let i = 0; i < textEffect.length; i++) {
    if(textEffect[i].id === id){
      this.setState({text:textEffect[i].text});
      this.setState({effect:textEffect[i].efc});

    }
  }

  if (n === textEffect.length ) {
    id = 0;
  }
}

  render() {
    return (
      <div style={{transition: '0.3s',opacity:this.state.opacity}}>
      <div className='wrapper'>
        <h1 className='head-line'>Mi imamo kvalitet</h1>
        <p>
          Lorem ipsum dolor sit amet, omittam elaboraret pro no,
          has cu ubique latine, pri ne autem sadipscing appellantur.
          Tractatos cotidieque an duo. Usu ad odio eripuit consulatu.
          Putant consequat mea ut, vis quis constituam cu, ad error doctus expetendis quo.
          Per graeci appellantur voluptatibus an,
          mazim comprehensam in sea, ex aeterno debitis singulis nam.
        </p>
        <img src={`${food}`} alt='food' style={{width:'100%'}} />
        <div>
        <p>
          Lorem ipsum dolor sit amet, omittam elaboraret pro no,
          has cu ubique latine, pri ne autem sadipscing appellantur.
          Tractatos cotidieque an duo. Usu ad odio eripuit consulatu.
          </p>
          </div>

        <div className='content-flex'>
          <div className='home-text'>
            <p>
              Lorem ipsum dolor sit amet, omittam elaboraret pro no,
              has cu ubique latine, pri ne autem sadipscing appellantur.
              Tractatos cotidieque an duo. Usu ad odio eripuit consulatu.
            </p>
          </div>
          <div>
            <img src={`${food2}`} alt='food' className='flex-img' />
          </div>
        </div>



        <div className='content-flex'>
          <div>
              <p className='head-line'>Mudri ljudi jednom rekli:</p>
          </div>
          <div className='home-text'>
            <p className={`effect-text ${this.state.effect}`}>
              "{this.state.text}"
            </p>
          </div>
        </div>
      </div>


      </div>

    );
  }

}

export default Home;



