import React, { Component } from 'react';
import Button from './Button';
/*import axios from 'axios'*/

import Maps from './Maps';

const locations = [
  {
    location:'kraljevu',
    addres: 'Moja adresa kv',
    phone: 993332333,
    phone2: 773332333,
    email: 'moj@mail.com',
    lat:43.723805,
    lng:20.6863536,
  },
  {
    location:'kragujevcu',
    addres: 'Moja adresa kg',
    phone: '0993332333',
    phone2: '0773332333',
    email: 'moj@mail.com',
    lat:44.02963,
    lng:20.9142787
  },
  {
    location:'beogradu',
    addres: 'Moja adresa bg',
    phone: '0993332333',
    phone2: '0773332333',
    email: 'moj@mail.com',
    lat:44.7957455,
    lng:20.4543976
  }
]

class Contact extends Component{
  constructor(props){
    super(props)
    this.state = {
      name:'',
      email:'',
      message:'',
      opacity: 0,
      focusLabel:null,
      content:null,
      lat:null,
      lng:null,
      addres: null

    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
    this.handleClickContent = this.handleClickContent.bind(this);
  }
  componentDidMount(){
    const opacity = setInterval(()=>{
      this.setState({opacity:1})
      clearInterval(opacity);
    },100);
  }
  handleSubmit(event){
    event.preventDefault();
    const {name,email,message} = this.state;
    alert(`Ime:${name}, E-adresa:${email}, Poruka: ${message}`);
  /*  axios.get(`http://localhost:3000/index.php`,{
      data:{
        name:name,
        email:email,
        message: msg
      }

    }

  )
    .then((response)=> console.log(response))
    .catch(error => console.log(error))*/
  }
postSend(data){
  alert(data);
}
handleError(error){
  alert(error)
}
  handleChange(event){
      const stateName = event.target.name;
      this.setState({[stateName]:event.target.value })
  }

handleFocus(props){
  this.setState({focusLabel:props.target.name})
}

handleClickContent(props){
  this.setState({content:props.target.getAttribute('data-location')});
  this.setState({lat:props.target.getAttribute('data-lat') * 1});
  this.setState({lng:props.target.getAttribute('data-lng') * 1});
  this.setState({addres:props.target.getAttribute('data-addres') });


}
  render(){
    const {name,email,message,focusLabel,content} = this.state;
    return(
      <div>
      <div className='subMenu'  style={{justifyContent:'center'}}>
        <p className='head-line'>Mozete nas pronaci u</p>
      </div>
    <div className='wrapper' style={{opacity:this.state.opacity}}>

      <div className='locations'>
        {
          locations.map((i,index)=>{
            return(
              <div key={index}>
                <div className='subMenu                                                                                                                                                                                                                                                              '>
                  <span onClick={this.handleClickContent} data-location={i.location} data-addres={i.addres} data-lat={i.lat} data-lng={i.lng} className={`subMenu-link contact-submenu_link ${content === i.location ? 'active-subMenu' : ''}`}>{i.location}</span>
                </div>
                <div className={`location-content ${content === i.location ? ' content-show' : ''}`}>
                  <p>{i.addres}</p>
                  <p>{i.phone}</p>
                  <p>{i.phone2}</p>
                  <p>{i.email}</p>
                </div>
              </div>
            )
          })
        }
      </div>
      <div className='map-wrapper'>
      {this.state.lat !== null && <Maps center={{lat:this.state.lat, lng:this.state.lng}} text='TITLE' addres={this.state.addres} cls={{cls:'content-show'}} />}
      </div>
      <div className='form-wrapper'>
        <p className='head-line'>Pisite nam</p>
        <form onSubmit={this.handleSubmit}>
          <div className='contact-form'>
            <div className='contact-form_group'>
              <label htmlFor='name' className={`contact-label ${focusLabel === 'name' ? ' move_label' : ''} ${name.length > 0 ? ' move_label' : ''} `} >Ime</label>
              <input onChange={this.handleChange} onFocus={this.handleFocus} value={name} className='contact-form_input' type='text' name='name'/>
            </div>
            <div className='contact-form_group'>
              <label htmlFor='email' className={`contact-label ${focusLabel === 'email' ? ' move_label' : ''} ${email.length > 0 ? ' move_label' : ''}`}>E-adresa</label>
              <input onChange={this.handleChange} onFocus={this.handleFocus} value={email} className='contact-form_input' type='email' name='email'/>
            </div>
            <div className='contact-form_group'>
              <label htmlFor='message' className={`contact-label ${focusLabel === 'message' ? ' move_label' : ''} ${message.length > 0 ? ' move_label' : ''}`}>Poruka</label>
              <textarea className='contact-form_textarea' cols='3' rows='8' onChange={this.handleChange} onFocus={this.handleFocus} value={message} name='message'></textarea>
            <Button className='button' type='submit' children='Posalji' />
          </div>
          </div>
        </form>
      </div>
    </div>
  </div>
    )
  }
}
export default Contact;



